'use client';
import React, { useState } from 'react';

export default function ContactForm() {
    const [form, setForm] = useState({ name: '', email: '', phone: '', message: '' });
    const [submitted, setSubmitted] = useState(false);
    const [error, setError] = useState('');

    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        setForm({ ...form, [e.target.name]: e.target.value });
    };

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        if (!form.name || !form.email || !form.message) {
            setError('Please fill in all required fields.');
            return;
        }
        setError('');
        setSubmitted(true);
        // Here you would handle the actual submission (API call, etc.)
    };

    return (
        <section className="w-full flex justify-center py-10 bg-white">
            <form onSubmit={handleSubmit} className="w-full max-w-2xl bg-[#FFF4FB] rounded-2xl shadow-lg p-8 flex flex-col gap-6">
                <h2 className="text-2xl font-bold text-[#632075] mb-2 text-center">Send us a message</h2>
                {error && <div className="text-red-500 text-center">{error}</div>}
                {submitted ? (
                    <div className="text-green-600 text-center text-lg">Thank you for contacting us! We will get back to you soon.</div>
                ) : (
                    <>
                        <input
                            type="text"
                            name="name"
                            placeholder="Your Name*"
                            value={form.name}
                            onChange={handleChange}
                            className="border border-gray-300 rounded-lg px-4 py-3 text-lg focus:outline-none focus:ring-2 focus:ring-[#632075]"
                            required
                        />
                        <input
                            type="email"
                            name="email"
                            placeholder="Your Email*"
                            value={form.email}
                            onChange={handleChange}
                            className="border border-gray-300 rounded-lg px-4 py-3 text-lg focus:outline-none focus:ring-2 focus:ring-[#632075]"
                            required
                        />
                        <input
                            type="tel"
                            name="phone"
                            placeholder="Your Phone"
                            value={form.phone}
                            onChange={handleChange}
                            className="border border-gray-300 rounded-lg px-4 py-3 text-lg focus:outline-none focus:ring-2 focus:ring-[#632075]"
                        />
                        <textarea
                            name="message"
                            placeholder="Your Message*"
                            value={form.message}
                            onChange={handleChange}
                            className="border border-gray-300 rounded-lg px-4 py-3 text-lg min-h-[120px] focus:outline-none focus:ring-2 focus:ring-[#632075]"
                            required
                        />
                        <button
                            type="submit"
                            className="bg-[#632075] hover:bg-[#B41E8E] text-white font-semibold px-8 py-4 rounded-lg text-xl transition"
                        >
                            Send Message
                        </button>
                    </>
                )}
            </form>
        </section>
    );
} 